.link-selector{
	position: fixed;
    z-index: 2;
    top: 75px;
    left: 33%;
    width: 400px;
    padding: 10px;
    border-radius: 5px;
    overflow: hidden;
    min-height: 200px;
}

.link-selector button.add{
	position: absolute;
    top: 45px;
    right: 10px;
}

.link-selector h1.semester{
	font-size: 40px;
}

.link-selector h5{
	margin: 0;
}

.link-selector hr{
	margin: 0;
}

.link-selector button.cancel{
	position: absolute;
    background: none;
    border: none;
    top: 0;
    right: 5px;
}

.link-selector .class-items{
	max-height: 150px;
    overflow: scroll;
    padding: 5px;
    border-radius: 5px;
}

.link-info{
	margin: 5px 0;
	border-radius: 5px;
	padding: 5px;
}